	  			let url = ''
	  			for(let key in this.searchData){
	  				if(this.searchData[key]){
	  					url = url + '\/' + this.searchData[key]
	  				} else {
	  					url = url + '\/' + this.searchData[key]
	  				}
	  			}

	  			return this.pageType + url
	  		},
