	  			this.$http.get('/srpms-outline/' + this.getUrl()+'\/'+ '1')
	  			.then((res)=>{
	  				res.data.forEach((item)=>{
	  					if(item.proCategory){
	  						let str0 = item.proCategory
	  						let str1 = str0.substr(1, str0.length - 2)
	  						item.proCategory = str1.split(',')
	  					}
	  				})
	  				this.tableData = res.data || []
	  			})
	  			.catch((err)=>{

	  			})
	  		},
	  		// 上传成功
	  		handleUploadSuccess () {
	  			this.$message({
		          message: '导入成功！',
		          type: 'success'
		        })
	  		},
	  		formatterType (val) {
	  			let item = this.allTypes.filter((o)=> {
	  				return String(o.value) === String(val) 
	  			})

	  			return item && item.length ? item[0].label:''
	  		},
	  		handleAdd () {
	  			let newRow = {}
	  			let newRowShow = []
	  			TABLEKEYS.forEach((key)=>{
	  				newRow[key] = ''
	  				newRowShow.push(false)
	  			})
	  			// 添加索引
	  			newRow['index'] = this.tableData.length
	  			this.tableInputShow.push(newRowShow)
	  			this.tableData.push(newRow)
	  		},
	  		handleDelete () {
	  			if(!this.multipleSelection.length) {
	  				this.$message({
			          message: '请选择要删除的项目',
			          type: 'warning'
			        })
			        return
	  			}
	  			// 模拟删除选中
	  			this.multipleSelection.forEach((item)=>{
	  				this.tableData.splice(item.index,1)
	  			})
	  			this.multipleTable = []	
	  		},
	  		handleSearch () {
	  			if(!this.validateSaveFlag()) return
	  			// 发起请求
	  			this.getTableData()
	  		},
	  		// 数据改变之后保存索引值到数组中
	  		handleValueChange (rowIdx) {
	  			this.savedFlag = false
	  			let isInArray = this.unsavedData.findIndex((o)=>{
	  				return o === rowIdx
	  			})

	  			if (isInArray === -1) {
	  				this.unsavedData.push(rowIdx)
	  			}
	  		},
	  		validateSaveFlag () {
	  			if (!this.savedFlag) {
	  				this.dialogShow = true
	  				return false
	  			} else {
	  				return true
	  			}
	  		},
	  		// 不保存数据查询
	  		handleLeftClick () {
	  			this.savedFlag = true
	  			this.dialogShow = false
	  			this.unsavedData = []
	  			// this.handleSearch()
	  			this.getTableData()
	  		},
	  		getOrg(){
	  			let txt = ''
	  			this.orgOptions.forEach((item)=>{
	  				if(String(item.id) === this.titleData.orgId){
	  					txt = item.label
	  				}
	  			})

	  			return txt
	  		}
	  	},
	  	computed: {
	  		activeorgIdName () {
	  			let idx = this.orgOptions.findIndex((o)=>{
	  				return o.id === this.searchData.orgId
	  			})
	  			

	  			return idx !== -1 ? this.orgOptions[idx].label:''
	  		},
	  		exportTitle () {
	  			let year =  this.titleData.year?  this.titleData.year + '年':''
	  			let month = this.titleData.month? this.titleData.month + '月':''
	  			let orgName = this.getOrg()

	  			return '确定导出：'+ orgName + year + month + '的模板?'
	  		}
	  	}
	}
</script>
<style lang="less">
	.new-title-page{
		padding-bottom: 60px;
		.pl80{padding-left: 80px;}
		.sub-title{
	    	margin: 30px 0;
	    	font-size: 18px;
			color: #63B4FF;
			letter-spacing: -0.27px;
			line-height: 18px;
			>span{
				display: inline-block;
				position: relative;
				top: -2px;
				width: 4px;
				height: 17px;
				margin-right: 10px;
				vertical-align: middle;
				background-color: #63B4FF;
			}
	    }
		.search-item{
			margin-bottom: 20px;
			>span{
				display: inline-block;
				height: 30px;
				width: 105px;
				line-height: 30px;
				font-size: 16px;
				color: #696969;
				text-align: right;
			}

		}
		.table-item{
			display: table-cell;
		}
		.table-title{
			margin-bottom: 20px;
		}
		.el-table .cell{
			line-height: 30px;
		}

		.opt-box{
			// margin: 40px 0 20px 14px;
		}
		.search-box{
			
		}
		@media screen and (max-width: 1600px) {
			.dt-select,.dt-input{
				width: 160px;
			}
			.table-item{
				padding-left: 10px;
				&.pl0{padding-left: 0;}
			}
	    }
	}
</style>


